			if(this.autoHideEmptyViewWhenLoading){
				if(this.isAddedData && !this.firstPageLoaded && !this.loading){
					return true;
				}
			}else{
				return true;
			}
			if(!this.autoHideEmptyViewWhenPull && !this.isUserReload){
				return true;
			}
			return false;
